.sidenav {
  width: 80px;
  position: fixed;
  z-index: 1;
  top: 50px;
  right: 25px;
  background: #262626;
  overflow-x: hidden;
}

.sidenav p {
  padding: 10px 1px 7px 1px;
  text-decoration: none;
  font-size: 15px;
  color: #ffffff;
  display: block;
  text-align: center;
  font-family: "Apple Symbols";
  font-weight: "Lighter";
}
.name {
  font-size: 75px;
  color: #ffffff;
  font-family: Apple Symbols;
  font-weight: Lighter;
  top: 5px;
}
.role {
  font-size: 65px;
  color: #ffffff;
  font-family: Apple Symbols;
  font-weight: Lighter;
}
.active {
  background-color: #46a49f;
  font-weight: bold;
}
.sidenav p:hover {
  color: #46a49f;
}
@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 5px;
  }
  .sidenav a {
    font-size: 8px;
  }
}
